@import url('https://fonts.googleapis.com/css2?family=Atkinson+Hyperlegible:ital,wght@0,400;0,700;1,400;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Atkinson+Hyperlegible:ital,wght@0,400;0,700;1,400;1,700&family=Bebas+Neue&display=swap');
main{
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
}
#movies{
    font-family: 'Atkinson Hyperlegible', sans-serif;
    color: white;
    width:200px;
    height: 300px;
    margin: 10px;
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    transition: all 200ms ease;
    cursor: pointer;

}


#movies:hover{
    transform: scale(1.08) rotate(2deg);
}

#movies span{
    background-color: rgb(19, 18, 18);
    filter: opacity(-1px);
    height: 100%;
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    padding: -10px;
   transform: translateY(260px);
    color: white;
    transition: all 200ms ease;

}

#movies img{
    height: 87%;
    width: 100%;
}
#movies span h4{
    margin-top: 10px;
}

#first-blur{
    position: fixed;
    right: 20px;
    top: 100px;
    height: 200px;
    width: 200px;
    background-color: white;
    filter:blur(70px);
    z-index: -9999999999;
}

#second-blur{
    position: fixed;
    left: 20px;
    top: 500px;
    height: 200px;
    width: 200px;
    background-color: white;
    filter:blur(50px);
    z-index: -9999999999;
}